.containerTasks {
  flex: 1;
  flex-grow: 1;
  flex-shrink: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 30px;
  box-sizing: border-box;
}

@media (max-width: 500px) {
  .containerTasks {
    padding: 10px;
  }
}
